import HexKeyboard from "./HexKeyboard.vue"
import langvue from "../../plugins/langVue";
import { createApp } from "vue";
// Hex
const showHexkybrd = (props={}) => {
    if(typeof(props)!="object") throw new Error("props must be a object")
    return new Promise((resolve, reject) => { //promiseokresolvenoreject
        // 
        let dom = createApp(HexKeyboard,props).use(langvue).mount(document.createElement("div"));
        // 
        document.body.appendChild(dom.$el);
        //  
        dom.onEnter = function (x) {
                resolve(x)
        }
        dom.onReject = function (x) {
                reject(x)
        }
    })
}
export default showHexkybrd