import {createApp} from "vue"
import langvue from "../../plugins/langVue"
import DateTimeThree from './LeetxDateTimeThree.vue'
//  Confirm()  XtxConfirm 

const showGetDatetimeThree = (props={}) => {
    if(typeof(props)!="object") throw new Error("props must be a object")
    return new Promise((resolve, reject) => { //promiseokresolvenoreject
        // 
        let dom = createApp(DateTimeThree,props).use(langvue).mount(document.createElement("div"));
        // 
        document.body.appendChild(dom.$el);
        // 
        dom.onConfirm = function (x) {
            resolve(x)
        }
        dom.onCancel = function (err) {
            reject(err)
        }
    })
}
export default showGetDatetimeThree