import { createApp } from "vue";
import langvue from "../../plugins/langVue";
import SlopeCal from './SlopeCal.vue'
//  Confirm()  XtxConfirm 
const slopecal = (props) => {
    return new Promise((resolve, reject) => { //promiseokresolvenoreject
        // 
        let dom = createApp(SlopeCal,props).use(langvue).mount(document.createElement("div"));
        // 
        document.body.appendChild(dom.$el);
        // 
        dom.onConfirm = (x) => {
            // 
            resolve(x)
            // document.body.removeChild(dom.$el)
            // dom =null
        }
        dom.onCancel = function (err) {
            // 
            reject(err)
            // document.body.removeChild(dom.$el)
            // dom =null
        }
    })
}
export default slopecal